import React from 'react';
import './GranaryComponent.css';

export default class GranaryComponent extends React.Component {
  
  sampleData = [
    {name:"farro", wanted: true, have: true, isPerishable: false},
    {name:"grapes", wanted: true, have: false, isPerishable: true},
    {name:"olive oil", wanted: true, have: true, isPerishable: false},
    {name:"beans", wanted: true, have: true, isPerishable: false},
    {name:"jerkey", wanted: true, have: true, isPerishable: false},
    {name:"feta", wanted: true, have: false, isPerishable: true},
    {name:"parsley", wanted: true, have: true, isPerishable: true},
    {name:"cheese curds", wanted: false, have: false, isPerishable: true},
  ];
  
  constructor(props){
    super(props);
    this.state = {
      data: this.sampleData,
    }
  }

  pressX(name) {
    for (let item of this.state.data){
      if (item.name == name){
        item.wanted = false;
      }
    }
    this.setState({data:this.state.data});
  }

  addToWishList(name) {
    for (let item of this.state.data){
      if (item.name == name){
        item.wanted = true;
        item.have = false;
      }
    }
    this.setState({data:this.state.data});
    //wanted = 1
    //have = 0
  }

  addToPantry(name) {
    for (let item of this.state.data){
      if (item.name == name){
        item.wanted = true;
        item.have = true;
      }
    }
    this.setState({data:this.state.data});
    //wanted = 1
    //have=1
  }

  toggleHave(name) {
    for (let item of this.state.data){
      if (item.name == name){
        item.have= !item.have;
      }
    }
    this.setState({data:this.state.data});
    //isHave=!isHave
  }

  togglePerishable(name) {
    for (let item of this.state.data){
      if (item.name == name){
        item.isPerishable = !item.isPerishable;
      }
    }
    this.setState({data:this.state.data});
    //isPerishable=!isPerishable
  }
  
  render() {

    let wishList = [];
    let pantry = [];
    let longStorage = [];

    for (let item of this.sampleData){
      if (item.wanted){
        if (!item.have){
          wishList.push(item);
        } else if (item.isPerishable){
          pantry.push(item);
        } else {
          longStorage.push(item);
        }
      }
    }

    const wishListItems = wishList.map((item) =>
      <li key={item.name} className="flex">
        {item.name} 
        <div className="hover-btn" onClick={()=>this.pressX(item.name)}>X</div>
        <div className="hover-btn" onClick={()=>this.toggleHave(item.name)}>&gt;</div>
      </li>
    );
    
    const pantryItems = pantry.map((item) =>
      <li key={item.name} className="flex">
        <div className="hover-btn" onClick={()=>this.pressX(item.name)}>X</div>
        <div className="hover-btn" onClick={()=>this.toggleHave(item.name)}>&lt;</div>
        <div className="hover-btn" onClick={()=>this.togglePerishable(item.name)}>V</div>
        {item.name}
      </li>
    );

    const longStorageItems = longStorage.map((item) =>
      <li key={item.name} className="flex">
        <div className="hover-btn" onClick={()=>this.pressX(item.name)}>X</div>
        <div className="hover-btn" onClick={()=>this.toggleHave(item.name)}>&lt;</div>
        <div className="hover-btn" onClick={()=>this.togglePerishable(item.name)}>^</div>
        {item.name}
      </li>
    );

    return (
      <div className="my-bg granary">
        Granary
        <div className="flex">
          <div className="my-bg">
            WishList
            <ul>{wishListItems}</ul>
          </div>
          <div>
            <div className="my-bg">
              Pantry
              <ul>{pantryItems}</ul>
            </div>
            <div className="my-bg">
              Long Term
              <ul>{longStorageItems}</ul>
            </div>
          </div>
        </div>
      </div>
    )
  }
}